import React from 'react'
import GettingBox from '../components/GettingBox'

const Gettings = () => {
    return (
        <div id="gettings">
            <h1
                class="py-16 font-bold text-4xl text-center text-gray-800"
            >
                What will you get?
            </h1>
            <div class="flex flex-col bg-white m-auto p-auto">

                <div
                    class="flex overflow-x-scroll pb-10 hide-scroll-bar"
                >
                    <div
                        class="flex flex-nowrap lg:mx-40 md:mx-20 mx-10 "
                    >
                        <GettingBox color="bg-red-300" text="Job Guarantee Program" />
                        <GettingBox color="bg-pink-300" text="Instant chat support on Whatsapp" />
                        <GettingBox color='bg-yellow-300' text="Within 3hrs chat support" />
                        <GettingBox color="bg-purple-300" text="Within 3hrs mail support" />
                        <GettingBox color="bg-gray-300" text="Video Solution to problems" />
                        <GettingBox color="bg-indigo-300" text="Post course 18 month access and support" />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Gettings
