import { motion } from 'framer-motion'
import React from 'react'

const GettingBox = ({text, color}) => {
    return (
        <motion.div class={`inline-block mx-4`}>
            <div class={`${color} w-64 h-64 max-w-xs overflow-hidden rounded-lg text-center shadow-md bg-white hover:shadow-xl cursor-pointer px-10 transition-shadow duration-300 ease-in-out flex items-center`}>
                <p className="text-center font-semibold text-xl text-gray-50">{text}</p>
            </div>
        </motion.div>
    )
}

export default GettingBox
